.promo {
  margin: 0;
  padding: 200px 0 0 0;
  width: 100%;
  background: linear-gradient(to right, #fa6579, #f43850);
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative;
  box-sizing: border-box;
  z-index: 1;
}

.promo__title {
  margin: 0;
  padding: 0;
  font-style: normal;
  font-weight: 700;
  font-size: 65px;
  line-height: 60px;
  color: #ffffff;
  text-align: center;
  text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
}

.promo__subtitle {
  margin: 0;
  padding: 10px 0 60px 0;
  font-style: normal;
  font-weight: 400;
  font-size: 42px;
  line-height: 38px;
  color: #ffffff;
  box-sizing: border-box;
}

.promo__promo-container {
  margin: 0;
  padding: 0 0 60px 0;
  justify-content: space-between;
  display: flex;
  flex-direction: row;
  box-sizing: border-box;
}

.promo__products {
  margin: 0;
  padding: 0 0 0 140px;
  display: flex;
  position: relative;
}


.promo__brush-img-pink {
  margin: 0;
  padding: 0;
  width: 217px;
  height: 360px;
  position: absolute;
  top: 70px;
  right: 245px;
}

.promo__brush-img-blue {
  margin: 0;
  padding: 0 0 0 239px;
}

.promo__brush-img-yellow {
  width: 184px;
  height: 233px;
  position: absolute;
  top: 200px;
  left: 630px;
  padding: 0 36px 0 0;
}

.promo__bestseller-container {
  margin: 0;
  padding: 0 0 0 151px;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  justify-content: flex-end;
  box-sizing: border-box;
  list-style: none;
}

.promo__bestseller {
  margin: 0;
  padding: 0 0 20px 0;
  align-self: center;
  box-sizing: border-box;
}

.promo__bestseller-title {
  margin: 0;
  padding: 0;
  font-style: normal;
  font-weight: 900;
  font-size: 40px;
  line-height: 44px;
  color: #ffffff;
}

.promo__button-buy {
  margin: 0;
  padding: 0;
  padding: 23px 65px 23px 65px;
  background-color: #7DD3DF;
  border-radius: 20.768px;
  border: inherit;
  font-style: normal;
  font-weight: 500;
  font-size: 32px;
  line-height: 20px;
  align-self: flex-start;
  box-sizing: border-box;
  cursor: pointer;
  transition: background-color 0.3s linear;
}

.promo__button-buy:hover {
  background-color: #6EB6CD;
}

.promo__button-buy:active {
  background-color: #2CA4BE;
}

.promo__button-link-buy {
  margin: 0;
  padding: 0;
  text-decoration: none;
  color: #ffffff;
  width: 100%;
  height: 100%;
  display: block;
  cursor: pointer;
}

.promo__bestseller-desc {
  margin: 0;
  padding: 20px 0 0 0;
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 24px;
  color: #ffffff;
  align-self: flex-start;
}

.promo__bestseller-desc_newline {
  padding: 0;
}

.promo__wrapper {
  background-color: inherit;
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: -1;
  overflow: hidden;
  background-image: url(../../images/solomeya.svg);
  background-position: 50%;
  background-repeat: no-repeat;
}

.promo__element-circle-pink {
  margin: 0;
  padding: 0;
  background-image: url(../../images/circle-pink.svg);
  background-size: cover;
  background-repeat: no-repeat;
  width: 728px;
  height: 728px;
  position: absolute;
  right: -375px;
  top: -300px;
  z-index: -1;
}

.promo__element-circle-blue {
  margin: 0;
  padding: 0;
  background-image: url(../../images/circle-blue.svg);
  background-size: cover;
  background-repeat: no-repeat;
  width: 262px;
  height: 262px;
  position: absolute;
  z-index: -1;
  right: 69.4%;
  top: 11%;
  z-index: -1;
}

.promo__element-circle-violet {
  margin: 0;
  padding: 0;
  background-image: url(../../images/violet.svg);
  background-size: cover;
  background-repeat: no-repeat;
  width: 354px;
  height: 354px;
  position: absolute;
  z-index: -1;
  right: -15%;
  top: 62%;
  z-index: -1;
  display: none;
}

.promo__element-circle-yellow {
